import back from '@/components/back'
export default {
    components:{
        back,
    },
    data(){
        return {
			info:null,
            list: [[]],
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
			item: [],
			show: false,
			ServiceItemName:'',
			Price:'',
			NumValue:'',
			Description:null,
			ServiceItemId:this.$route.query.ServiceItemId,
			customSevice:this.$store.state.customSevice,
			addArr:[]
        }
    },
    created(){
		if(this.$route.query.ServiceItemId != undefined){
			this.customSevice.forEach((a,k) => {
			    if(a.ServiceItemId == this.ServiceItemId){
					this.ServiceItemName=a.ServiceItemName
					this.Price= Number(a.Price)
					this.NumValue=a.NumValue
					this.Description=a.Description
					this.item[0] = a.DoctorServiceWayName
				}
			})
		}
        this.pull(false)
    },
    mounted(){

    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
		changeNumber() {
			  if (this.NumValue.indexOf('.') != -1) {
					return this.$toast('服务次数需是整数')
			  }
		},
        pull(h){
             this.$post('PlatFormAPI/ServicePackItem/QueryDoctorServiceWays',this.$pick({
                DrId: this.user.Data.User.DoctorId,
                DrType: this.user.Data.User.DrType,
            })).then(o => {
				this.info = o
				o.forEach(v => {
				    this.list[0].push(v.DoctorServiceWayName)
				})
            })
        },
        send(i){
			if(!this.ServiceItemName){
				return this.$toast('请输入服务名称')
			}
			if(!this.Price){
				return this.$toast('请输入服务单价')
			}
			if(!this.NumValue){
				return this.$toast('请输入服务次数')
			}
			if(!this.Description){
				return this.$toast('请填写服务简介')
			}
			let numb = Number(this.NumValue).toString()
			
			if(Number(this.NumValue) < 1){
				return this.$toast('服务次数需是整数且大于1')
			}else{
				if (numb.indexOf('.') != -1) {
					return this.$toast('服务次数需是整数且大于1')
				}
			}
			if(!this.item[0]){
				return this.$toast('请输入服务方式')
			}
			if(this.Description != null && this.Description.length > 200){
				return this.$toast('服务简介不能超过200字')
			}
			let obj = []
			this.info.forEach(v => {
			    if(v.DoctorServiceWayName == this.item[0]){
			        obj = v
			    }
			})
			obj.GoodsSource = 4
			obj.ServiceItemName=this.ServiceItemName
			obj.Price = this.Price
			obj.NumValue = Number(this.NumValue)
			obj.Description=this.Description
			obj.NumTypeName = "次"
			obj.isChecked = 1
			if(this.ServiceItemId){
				obj.ServiceItemId = obj.ServiceItemId
			}
			this.addArr.splice(0, 1, obj) 
			if(this.customSevice.length > 0){
				let t = 0
				this.customSevice.forEach((a,k) => {
				    if(a.ServiceItemId == this.ServiceItemId && a.GoodsSource > 1){
						t = 1
						this.customSevice.splice(k, 1, obj)
					}
				})
				if(t==0){
					this.customSevice.push(obj)
				}
			}
			console.log(this.customSevice)
            this.$store.dispatch('pushcustomSevice',this.customSevice.length > 0?this.customSevice:this.addArr)
            this.$router.back()
        },
		goback(){
			this.$router.back()
		}
    }
}



